import tuiSwipeAction from "@/components/swipe-action/swipe-action"
import tuiButton from "@/components/extend/button/button"
import tuiNumberbox from "@/components/numberbox/numberbox"
import tuiIcon from "@/components/icon/icon"
import tuiDivider from "@/components/divider/divider"
import tuiLoadmore from "@/components/loadmore/loadmore"
import {
	ajax,
	getSecret
} from '@/common/ajax/ajax.js'
export default {
	components: {
		tuiSwipeAction,
		tuiButton,
		tuiNumberbox,
		tuiIcon,
		tuiDivider,
		tuiLoadmore
	},
	data() {
		return {
			userProvider: '微信',
			store: '',
			storeName: '总部旗舰店',
			tagIndex: 1,
			showTag: false,
			userInfo: null,
			headerPosition: "fixed",
			secret: '',
			openid: '',
			dataList: [],
			actions: [{
				name: '删除',
				color: '#fff',
				fontsize: 28,
				width: 64,
				background: '#F82400'
			}],
			isEdit: false,
			productList: [],
			pageIndex: 1,
			loadding: false,
			pullUpOn: true,
			// 购物车总数量
			count: 0,
			// 总金额
			totalMoney: 0,
			totalPrice: 0,
			// 全选
			allchecked: true,
			// 单项：
			itemchecked: false,
			// 选择的商品
			chooseList: [],
			// 删除商品ID
			delId: '',
			act: '',
			tips: '',
			msg: '',
			// 支付金额
			allprice: 0,
			scrollTop: 0,
			newscrollTop: 0,
			productIdList: [], //选择的商品Id
			goodsList: [],
			couponid: '',
			pronum: 1
		}
	},
	onPageScroll(e) {
		this.newscrollTop = e.scrollTop;
		if (e.scrollTop >= 0) {
			this.headerPosition = "fixed";
		} else {
			this.headerPosition = "absolute";
		}
	},
	onLoad() {
		this.userProvider = this.$xm.userProvider();
		console.log(this.userProvider);
	},
	onShow() {
		this.allchecked = true;
		this.tagIndex = 1
		this.store = 10001;
		this.changeData();
		this.getproList();
		let code = uni.getStorageSync('store').store_code;
		if (code == 10001) {
			this.showTag = false;
		} else if (typeof(code) == 'undefined') {
			this.showTag = false;
		} else {
			this.showTag = true;
		}
		uni.getStorage({
			key: 'userInfo',
			success: (res) => {
				console.log(res);
				this.userInfo = res.data;
			}
		});

	},
	methods: {
		toGood(e) {
			uni.navigateTo({
				url: '../../goods/goods?gid=' + e.proid
			})
		},
		chooseStore(e) {
			this.tagIndex = e;
			if (e == 1) {
				this.store = 10001;
				this.storeName = '总部旗舰店'
				this.changeData();
				this.productList = [];
				this.getproList()
			} else {
				this.store = uni.getStorageSync('store').store_code;
				this.storeName = uni.getStorageSync('store').store_name;
				this.changeData();
				this.productList = [];
				this.getproList()
			}

		},
		changeData() {
			let data = {
				store: this.store
			}
			this.$xm.post('/Cart', data, res => {
				this.dataList = res.prolist;
				this.$xm.requestImg(this.dataList);
				let totalAll = []
				this.dataList.forEach(ele => {
					ele.checked = true;
					totalAll.push(ele.proid + '/' + ele.pronum)
				})
				this.goodsList = totalAll
				this.totalPrice = res.allprice;
				this.totalMoney = res.allprice;
				this.allchecked = true;
				this.count = res.count;
				this.tips = res.tips;
				this.getmatchOptimals();
				if (this.count !== null) {
					this.count = this.count.toString()
					uni.setTabBarBadge({
						index: 2,
						text: this.count
					})
				} else {
					uni.removeTabBarBadge({
						index: 2,
					})
				}
				uni.setStorage({
					key: 'cart',
					data: this.count,
					success: (res) => {
						console.log(res);
					}
				})

			})
		},
		getuserinfo() {
			// 登录
			uni.login({
				provider: this.userProvider,
				success: (res) => {
					console.log(res)
					if (res.code) {
						var code = res.code
						uni.getUserInfo({
							success: (res) => {
								this.userInfo = res.userInfo;
								let params = {
									encryptedData: res.encryptedData,
									iv: res.iv,
								}
								this.$xm.post('/Index/getUnionid', params, (res) => {
									const user = uni.getStorageSync('user');
									user.unionid = res.unionid;
									uni.setStorage({
										key: 'user',
										data: user,
										success: (res) => {
											console.log(res);
										}
									});
								})
								uni.setStorage({
									key: 'userInfo',
									data: res.userInfo,
									success: (res) => {
										console.log(res);
									}
								});
								uni.navigateTo({
									url: '../../order/confirmation?store=' + this.store
								})
							},
							fail: res => {
								// 获取失败的去引导用户授权 
							}
						})

					} else {}
				},
				fail: (res) => {
					console.log(res)
				}
			})
		},

		// 获取优惠活动
		getmatchOptimals() {
			console.log(this.goodsList)
			let data = {
				goods_list: this.goodsList
			}
			this.$xm.post('/Coupon/match_optimalss', data, res => {
				if (res) {
					this.msg = res.data.msg;
					this.allprice = res.data.money;
					if (res.data.type == 2) {
						this.couponid = res.id
					}
				} else {
					this.allprice = 0;
				}

			})

		},
		getcalcmoney() {
			let data = {
				goods_list: this.goodsList,
				store: this.store
			}
			this.$xm.post('/Cart/calcmoney', data, (res) => {
				this.totalMoney = res.money
			})
		},
		// 获取猜你喜欢
		getproList() {
			let data = {
				store: this.store
			}
			this.$xm.post('/Cart/guess', data, (res) => {
				res.data.map(ele => {
					ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';
				});
				this.productList = this.productList.concat(res.data);
			})
		},
		itemchange(e) {
			this.productIdList = e.detail.value;
			if (this.productIdList.length == this.dataList.length) {
				this.allchecked = true;
				let arr1 = []
				this.dataList.forEach(ele => {
					arr1.push(ele.proid + '/' + ele.pronum)
				})
				this.goodsList = arr1;
				this.getcalcmoney();
				this.getmatchOptimals()
			} else {
				this.allchecked = false;
				let idList = this.productIdList.toString();
				let arr2 = [];
				this.dataList.forEach(ele => {
					if (idList.indexOf(ele.proid) !== -1) {
						arr2.push(ele.proid + '/' + ele.pronum)
						ele.checked = true;
					} else {
						ele.checked = false;
					}
				})
				this.goodsList = arr2;
				this.getcalcmoney();
				this.getmatchOptimals()
			}


		},
		checkboxChange(e) {
			this.allchecked = !this.allchecked;
			if (this.allchecked) {
				this.totalMoney = this.totalPrice;
				let allarr = [];
				this.dataList.forEach(ele => {
					ele.checked = true;
					allarr.push(ele.proid + '/' + ele.pronum)
				})
				this.goodsList = allarr;
				this.productIdList = this.dataList.map(ele => {
					return ele.proid
				})
			} else {
				this.dataList.forEach(ele => {
					ele.checked = false
				})
				this.goodsList = [];
				this.productIdList = [];
				this.totalMoney = 0;
			}
			this.getmatchOptimals()
		},
		changeNum(e) {
			this.delId = e.index;
			if (e.value == 0) {
				uni.showModal({
					title: '确定删除商品吗？',
					content: '',
					confirmText: '取  消',
					cancelColor: '#b2b2b2',
					confirmText: '确  定',
					confirmColor: '#2c2c2c',
					success: (res) => {
						if (res.confirm) {
							this.act = 'del';
							this.del();
						}
					},
				})
			} else {
				if (e.type == 'plus') {
					this.act = 'add';
					this.del()
				} else if (e.type == 'reduce') {
					this.act = 'reduce';
					this.del();
				}
			}


		},
		handlerButton(e) {
			this.delId = e.item.proid;
			this.act = 'del';
			this.del();
		},
		// 删除购物车
		del() {
			let params = {
				act: this.act,
				id: this.delId,
			}
			this.$xm.post('/Cart/ajax', params, res => {
				if (res.s == 1) {
					this.changeData()
				} else if (res.s == 0) {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					})
				}
			})
		},
		// 加入购物车
		joinCart(e) {
			let params = {
				proid: e.id,
				pronum: this.pronum,
				store: this.store
			};
			this.$xm.post('/Cart/add', params, (res) => {
				let result = res.msg;
				if (res.s == 1) {
					uni.showToast({
						title: result
					});
					this.changeData()
				} else if (res.s == 0) {
					uni.showToast({
						title: result,
						icon: 'none'
					});
				}
			})

		},
		delGoods() {
			if (this.allchecked) {
				this.productIdList = this.dataList.map(ele => {
					return ele.proid
				})
			}
			this.delId = this.productIdList;
			if (this.delId.length > 0) {
				uni.showModal({
					title: '确定删除商品吗？',
					content: '',
					confirmText: '取  消',
					cancelColor: '#b2b2b2',
					confirmText: '确  定',
					confirmColor: '#2c2c2c',
					success: (res) => {
						if (res.confirm) {
							this.act = 'del';
							this.del();
						}
					},
				})
			} else {
				uni.showToast({
					title: '没有选择要删除的商品！',
					icon: 'none'
				})
			}


		},
		detail(e) {
			uni.navigateTo({
				url: '../../goods/goods?gid=' + e.id
			});
		},
		topage() {
			uni.switchTab({
				url: '../category/category'
			})
		},
		btnPay() {
			if (this.allchecked) {
				this.productIdList = this.dataList.map(ele => {
					return ele.proid
				})
			}
			let data = {
				goods_list: this.productIdList,
				store: this.store
			}
			console.log(this.productIdList);
			if (this.productIdList.length > 0) {
				this.$xm.post('/Cart/xiangou', data, (res) => {
					if (res.s == 0) {
						uni.showToast({
							title: res.msg,
							icon: 'none'
						})
					} else {
						uni.navigateTo({
							url: '../../order/confirmation?store=' + this.store + '&goods_list=' + this.productIdList
						})
					}
				})

			} else {
				uni.showToast({
					title: '没有选择要结算的商品！',
					icon: 'none'
				})
			}


		}
	},

	onPullDownRefresh() {
		this.changeData()
		setTimeout(() => {
			uni.stopPullDownRefresh()
		}, 200)
	},
	onReachBottom() {
		this.getproList();
		setTimeout(() => {
			uni.stopPullDownRefresh()
		}, 200)

	},

}
